.ds-parent {
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.ds-container {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.ds-container > * {
    position: relative;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
}
